.resize-container {
  position: relative;
  min-width: 50px;
  min-height: 50px;

  .resize-handle {
    position: absolute;
    background: #2196F3;
    opacity: 0;
    transition: opacity 0.2s;

    &:hover {
      opacity: 0.8;
    }
  }

  &:hover .resize-handle {
    opacity: 0.5;
  }

  // 상하좌우
  .resize-handle.n {
    top: 0;
    left: 0;
    right: 0;
    height: 4px;
    cursor: ns-resize;
  }

  .resize-handle.e {
    top: 0;
    right: 0;
    bottom: 0;
    width: 4px;
    cursor: ew-resize;
  }

  .resize-handle.s {
    bottom: 0;
    left: 0;
    right: 0;
    height: 4px;
    cursor: ns-resize;
  }

  .resize-handle.w {
    top: 0;
    left: 0;
    bottom: 0;
    width: 4px;
    cursor: ew-resize;
  }

  // 모서리
  .resize-handle.ne {
    top: 0;
    right: 0;
    width: 8px;
    height: 8px;
    cursor: ne-resize;
  }

  .resize-handle.se {
    bottom: 0;
    right: 0;
    width: 8px;
    height: 8px;
    cursor: se-resize;
  }

  .resize-handle.sw {
    bottom: 0;
    left: 0;
    width: 8px;
    height: 8px;
    cursor: sw-resize;
  }

  .resize-handle.nw {
    top: 0;
    left: 0;
    width: 8px;
    height: 8px;
    cursor: nw-resize;
  }
}
